
































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CString from '@/components/links/CString.vue';
import CButton from '@/components/CButton.vue';
import PrefectureData from '@/typescript/PrefectureData.ts'

@Component({
  components: {
    CString,
    CButton,
  },
  head: {
    title: {
      inner: '都道府県一覧整形',
    },
  },
})
export default class Prefecture extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataFormat: string = '';
  private dataText: string = '';
  private dataSelectedSampleIndex: number = 0;
  private dataKeyRegExps: {
    iso: RegExp,
    name: RegExp,
    kana: RegExp,
    roman: RegExp,
    capital: RegExp,
    capital_kana: RegExp,
    capital_roman: RegExp,
    region: RegExp,
    region_kana: RegExp,
    region_roman: RegExp,
  } = PrefectureData.keyRegExps;
  private dataPrefectures: {
      iso: string,
      name: string,
      kana: string,
      roman: string,
      capital: string,
      capital_kana: string,
      capital_roman: string,
      region_index: number,
  }[] = PrefectureData.prefectures;
  private dataRegions: {
    name: string,
    kana: string,
    roman: string,
  }[] = PrefectureData.regions;
  private dataSamples: {
    name: string,
    description: string,
    format: string,
  }[] = [
    {
      name: 'none',
      description: '',
      format: '',
    },
    {
      name: 'names',
      description: 'カンマ区切りの都道府県名一覧',
      format: '{ name },',
    },
    {
      name: 'csv',
      description: 'CSVファイル用',
      format: '{ iso },{ name },{ kana },{ roman },{ capital },{ capital_kana },{ capital_roman },{ region },{ region_kana },{ region_roman },\n',
    },
    {
      name: 'select',
      description: 'HTML文書のselectタグ用',
      format: '<option value="{ roman }">{ name }</option>\n',
    },
    {
      name: 'table',
      description: 'HTML文書のtableタグ用',
      format: '<tr>\n    <td class="prefecture-iso">{ iso }</td>\n    <td class="prefecture-name">{ name }</td>\n    <td class="prefecture-kana">{ kana }</td>\n    <td class="prefecture-roman">{ roman }</td>\n    <td class="prefecture-capital">{ capital }</td>\n    <td class="prefecture-capital-kana">{ capital_kana }</td>\n    <td class="prefecture-capital-roman">{ capital_roman }</td>\n    <td class="prefecture-region">{ region }</td>\n    <td class="prefecture-region-kana">{ region_kana }</td>\n    <td class="prefecture-region-roman">{ region_roman }</td>\n</tr>\n',
    },
    {
      name: 'array',
      description: 'JavaScriptの配列',
      format: '{\n    iso: "{ iso }",\n    name: "{ name }",\n    kana: "{ kana }",\n    roman: "{ roman }",\n    capital: "{ capital }",\n    capital_kana: "{ capital_kana }",\n    capital_roman: "{ capital_roman }",\n    region: "{ region }",\n    region_kana: "{ region_kana }",\n    region_roman: "{ region_roman }",\n},\n',
    },
    {
      name: 'json',
      description: 'JSON形式',
      format: '{\n    "iso": "{ iso }",\n    "name": "{ name }",\n    "kana": "{ kana }",\n    "roman": "{ roman }",\n    "capital": "{ capital }",\n    "capital_kana": "{ capital_kana }",\n    "capital_roman": "{ capital_roman }",\n    "region": "{ region }",\n    "region_kana": "{ region_kana }",\n    "region_roman": "{ region_roman }"\n},\n',
    },
  ];

  // Methods
  private listenInput($event: Event): void {
    this.dataFormat = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenSampleSelect($event: Event): void {
    let index: number = Number((<HTMLInputElement>(<any>$event).target).value);
    this.dataFormat = this.dataSamples[index].format;
    this.dataSelectedSampleIndex = index;
  }
  private outputText(): void {
    this.dataText = '';
    if (this.dataFormat !== '') {
      let regExps: RegExp[] = Object.values(this.dataKeyRegExps);
      for (let i: number = 0; i < regExps.length; i++) {
        if (this.dataFormat.match(regExps[i])) {
          this.formatPrefectures();
          break;
        }
      }
    }
  }
  private formatPrefectures(): void {
    let text: string = '';
    for (let i: number = 0; i < this.dataPrefectures.length; i++) {
      let prefectureText: string = this.dataFormat;
      prefectureText = prefectureText.replace(this.dataKeyRegExps.iso, this.dataPrefectures[i].iso);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.name, this.dataPrefectures[i].name);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.kana, this.dataPrefectures[i].kana);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.roman, this.dataPrefectures[i].roman);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.capital, this.dataPrefectures[i].capital);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.capital_kana, this.dataPrefectures[i].capital_kana);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.capital_roman, this.dataPrefectures[i].capital_roman);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.region, this.dataRegions[this.dataPrefectures[i].region_index].name);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.region_kana, this.dataRegions[this.dataPrefectures[i].region_index].kana);
      prefectureText = prefectureText.replace(this.dataKeyRegExps.region_roman, this.dataRegions[this.dataPrefectures[i].region_index].roman);
      text += prefectureText;
    }
    this.dataText = text;
  }
}
